<template>
	<div>
		<div class="btn">
			<span>状态：</span>
			<el-select v-model="auditState" placeholder="请选择状态" @change="changeAudit">
				<el-option v-for="item in auditList" :key="item.value" :label="item.label" :value="item.value"></el-option>
			</el-select>
		</div>
		<div class="btn">
			<span>职位：</span>
			<el-select v-model="workerState" placeholder="请选择职位" @change="changeWorker">
				<el-option v-for="item in workerList" :key="item.value" :label="item.label" :value="item.value">
				</el-option>
			</el-select>
		</div>
		<div class="btn">
			<el-button type="primary" @click="search">搜索</el-button>
		</div>

		<el-table :data="tableDate" ref="multipleTable">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="用户ID" align="center"></el-table-column>
			<el-table-column prop="name" label="姓名" align="center"></el-table-column>
			<el-table-column prop="href" label="人脸图形" align="center">
				<template slot-scope="scope"><img :src="scope.row.href" style="max-width:180px;max-height:80px;" /></template>
			</el-table-column>
			<el-table-column prop="worker" label="是否为班主任" align="center"></el-table-column>
			<el-table-column prop="state" label="状态" align="center">
			</el-table-column>
			<el-table-column prop="created_at" label="创建时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center" width="300px">
				<template slot-scope="scope">
					<el-button type="primary" size="mini" @click="handleTeacher(scope.$index, scope.row)">查看详情</el-button>
					<el-button v-if="disAudit" type="primary" size="mini" @click="handleAudit(scope.$index, scope.row)">审核</el-button>
					<el-button type="danger" size="mini">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>

		<el-dialog :visible.sync="dialogTeacher" title="教师详情" width="500px">
			<el-form :model="form" label-width="80px">
				<el-form-item label="性别">
					<el-radio-group v-model="form.sex">
						<el-radio :label='1'>男</el-radio>
						<el-radio :label='2'>女</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="手机号">
					<el-input v-model="form.phone" :disabled="disabled"></el-input>
				</el-form-item>
				<el-form-item label="身份证号">
					<el-input v-model="form.id_card" :disabled="disabled"></el-input>
				</el-form-item>
				<el-form-item label="地址">
					<el-input v-model="form.address" type="textarea" :disabled="disabled"></el-input>
				</el-form-item>
				<el-form-item label="班级">
					<el-input v-model="form.class_id" :disabled="disabled"></el-input>
				</el-form-item>
				<!-- <el-form-item label="学科">
					<el-input v-model="form.name" :disabled="disabled"></el-input>
				</el-form-item> -->
			</el-form>
		</el-dialog>

		<el-dialog :visible.sync="dialogAudit" title="审核" width="20%" align="center">
			<div style="font-size: 20px; margin-bottom: 30px;">是否通过审核</div>
			<span>
				<el-button type="primary" @click="toAudit">通过</el-button>
				<el-button type="danger" @click="unAudit">不通过</el-button>
			</span>
		</el-dialog>

	</div>
</template>

<script>
	import API from '@/api/teacher.js'

	export default {
		name: 'teacher',
		data() {
			return {
				auditState: 4,
				state: '',
				auditList: [{
						value: 4,
						label: '全部'
					}, {
						value: 1,
						label: '待审核'
					},
					{
						value: 2,
						label: '已通过'
					},
					{
						value: 3,
						label: '未通过'
					}
				],
				workerState: 4,
				worker: '',
				workerList: [{
						value: 4,
						label: '全部'
					}, {
						value: 1,
						label: '职工'
					},
					{
						value: 2,
						label: '教师'
					},
					{
						value: 3,
						label: '保安员'
					}
				],
				tableDate: [{}],
				dialogTeacher: false,
				disabled: false,
				form: {},
				dialogAudit: false,
				disAudit: false,
				// 分页
				currentPage: 1,
				totalPage: 0,
				info_id: ''

			}
		},
		mounted() {
			this.search();
		},
		methods: {
			// 获取教师
			changeAudit() {
				var self = this;
				switch (self.auditState) {
					case 4:
						self.state = 0;
						break;
					case 1:
						self.state = 1;
						break;
					case 2:
						self.state = 2;
						break;
					case 3:
						self.state = 3;
				}
			},
			changeWorker() {
				var self = this;
				switch (self.workerState) {
					case 4:
						self.worker = 0
						break;
					case 1:
						self.worker = 1
						break;
					case 2:
						self.worker = 2
						break;
					case 3:
						self.worker = 3
				}
			},
			getState() {
				var self = this;
				self.tableDate.forEach(item => {
					switch (item.state) {
						case 1:
							item.state = '待审核';
							self.disAudit = true;
							break;
						case 2:
							item.state = '已通过';
							self.disAudit = false;
							break;
						case 3:
							item.state = '未通过';
							self.disAudit = false;
					}
					switch (item.worker) {
						case 0:
							item.worker = '否'
							break;
						case 1:
							item.worker = '否'
							break;
						case 2:
							item.worker = '是'
							break;
						case 3:
							item.worker = '否'
					}
				})
			},
			search() {
				var self = this;
				if (self.auditState && self.workerState) {
					API.teacher(1, self.state, self.worker).then(res => {
						self.tableDate = res.data;
						self.totalPage = res.total;
						self.currentPage = 1;
						this.getState()
					})
				}
			},

			// 操作
			handleTeacher(index, row) {
				var self = this;
				self.disabled = true;
				self.dialogTeacher = true;
				row.sex = Number(row.sex)
				self.form = row;
			},

			// 审核
			handleAudit(index, row) {
				var self = this;
				self.info_id = row.id
				console.log(self.info_id)
				self.dialogAudit = true;
			},
			toAudit() {
				var self = this;
				var userId1 = {
					state: 2,
					id: self.info_id
				}
				API.audit(userId1).then(res => {
					self.dialogAudit = false;
					self.$message.success('提交成功');
					self.search();
				})
			},
			unAudit() {
				var self = this;
				var userId2 = {
					state: 3,
					id: self.info_id
				}
				API.audit(userId2).then(res => {
					self.dialogAudit = false;
					self.$message.success('提交成功')
					self.search();
				})
			},
			// 分页
			handleCurrentChange(val) {
				var self = this;
				API.teacher(self.currentPage).then(res => {
					self.tableDate = res.data;
					this.getState()
				})

			},
		}
	}
</script>

<style lang="scss" scoped>

</style>
